@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,100italic,300italic");
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: currentColor; }

ul,
ol,
li {
  list-style: none; }

html,
body {
  font-family: "Roboto", "Microsoft YaHei", Helvetica, sans-serif;
  line-height: normal; }

.content.inactive {
  display: none; }

@charset "UTF-8";
/*arrow-down*/
#one .down-btn, #two .content .down-btn {
  position: absolute;
  left: 50%;
  bottom: 0.75em;
  transform: translateX(-50%);
  padding: 30px 50px 0;
  text-align: center; }
  #one .down-btn .icon, #two .content .down-btn .icon {
    display: inline-block;
    font-size: 44px; }

/*更多信息按钮*/
#two .content .btn, #five .content .btn {
  display: inline-block;
  padding: 0 2.25em;
  line-height: 3em;
  height: 3em;
  background-color: rgba(255, 255, 255, 0.3);
  opacity: 0.25; }

#two .content .btn-disabled, #five .content .btn-disabled {
  cursor: not-allowed; }

/*section背景图*/
#two .content h2 {
  margin-bottom: 1em;
  line-height: 1.5rem;
  font-size: 2rem;
  letter-spacing: -0.025em; }

#two .content p.desc {
  margin-bottom: 1em;
  font-size: 1.1rem;
  font-weight: 300; }

#two .content .quote {
  margin-bottom: 2em;
  padding: 0.5em 0 0.5em 2em;
  font-size: 1rem;
  font-style: italic;
  border-left: 4px solid rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.75); }

#two .content p.text {
  margin-bottom: 2em;
  line-height: 1.5em;
  font-size: 1rem;
  font-weight: 100;
  color: rgba(255, 255, 255, 0.75); }
  #two .content p.text span {
    color: #fff; }

@media screen and (max-width: 575.99px) {
  #two .content h2 {
    margin-bottom: 1.54rem;
    font-size: 1.5rem; }
  #two .content p.desc {
    font-size: 1rem; }
  #two .content .quote {
    margin: 0; } }

html,
body {
  height: 100%;
  color: #fff;
  font-size: 16px; }

.container {
  height: 100%; }
  .container > .item {
    height: 100%;
    background-color: #272833;
    background-image: linear-gradient(to top, rgba(23, 24, 32, 0.95), rgba(23, 24, 32, 0.95)); }

#one {
  position: relative;
  justify-content: center;
  flex: 1; }
  #one .bg {
    width: 100%;
    background: url("") no-repeat fixed center 0/cover; }
    #one .bg:before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-image: url("../assets/img/overlay.png"); }
  #one .header {
    display: flex;
