    padding-left: 24px;
    height: 50px;
    line-height: 50px;
    font-size: 1.1rem; }
    #one .header > .logo > a span {
      font-weight: 300; }
    #one .header > .logo > a sup {
      font-size: 0.8rem;
      font-weight: 300; }
    #one .header .nav {
      position: absolute;
      top: 0px;
      right: 20px;
      font-size: 1.1rem; }
      #one .header .nav .btn-default,
      #one .header .nav .btn-disabled {
        display: inline-block;
        padding: 0 1rem;
        height: 2rem;
        line-height: 2rem;
        font-weight: 100; }
      #one .header .nav .btn-default {
