        #one .header .nav .btn-default:hover {
          color: #e44c65; }
      #one .header .nav .btn-disabled {
        cursor: not-allowed;
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 2px;
        opacity: 0.3; }
  #one div.content {
    padding-right: 6.5rem;
    padding-bottom: 3.75rem;
    text-align: right; }
    #one div.content > h2 {
      margin-bottom: 10px;
      font-size: 2.5rem;
      font-weight: 300; }
    #one div.content > p {
      line-height: 40px;
      font-size: 1.25rem;
      font-weight: 300; }
    #one div.content > .logo img {
      display: inline-block;
      border-radius: 50%; }

@media screen and (max-width: 575.99px) {
  #one div.content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-right: 15px;
    text-align: center; }
    #one div.content > h2 {
      font-size: 1.5rem; }
    #one div.content > p {
      line-height: 1.75;
      font-size: 1rem; }
      #one div.content > p > br {
        display: none; }
    #one div.content > .logo img {
      height: 9rem;
      width: 9rem;
      border-radius: 50%; } }

#two {
  position: relative;
  overflow: hidden; }
  #two .bg {
    width: 100%;
    background: url("../assets/img/pic02.jpg") no-repeat fixed center 0/cover; }
    #two .bg:before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-image: url("../assets/img/overlay.png"); }
  #two .bg-mobile {
    display: none; }
  #two .content {
